.details {
  grid-area: details;
  display: flex;
  flex-direction: column;
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  margin: 10px;
  padding: 10px;
  font-size: 1rem;
  font-weight: 500;
  line-height: 24px;
  border-radius: 10px;
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.25);
}

.list {
  display: flex;
  flex-direction: column;
  margin: 0 10px;
  padding: 0;
}

.item {
  display: flex;
  gap: 10px;
  height: 40px;
  padding: 10px;
  color: #000000;
  cursor: pointer;
}

.socialIcon {
  font-family: 'social-icons' !important;
}

.item:hover .socialIcon {
  transform: scale(1.1);
}

.item:hover .link {
  color: #66CC66;
}